import React, { Component } from "react";
import  {auth} from "../../../../firebase/firebase";

class test_g_a_r extends Component {

    constructor() {
        super();


        this.state =
            {
                isLoaded:false,

            }
    }


    async  logout() {
        auth.signOut();
        window.location.href = '/';
    }

    render() {

        return(
            <div id="guideAttendReport" class="sec-design">
                <div id="guideAttendReport" class="form-design" name="guideAttendReport">
                    <div id="name-group" class="form-group">
                        <label id="date" class="title-input">   :</label>
                        <input type="date" class="form-control" name="insert-date" id="insert-date"/>
                        <button id="viewReport" class="btn btn-info" onClick=""></button>
                    </div>
                    <div id="name-group" class="form-group" dir="rtl">
                        <label id="insert-message" class="title-input">    :</label><br/>
                        <div id="stList" class="checkboxes"></div>
                    </div>
                    <button type="submit" id="confirm-form" class="btn btn-info" onClick="repConfirmation()"></button>
                    <button id="go-back" class="btn btn-info" onClick="loadMenu()"></button>
                </div>
            </div>
        )
    }
}





export default test_g_a_r;
