import React, { Component } from "react";

class test_g_f extends Component {

    constructor() {
        super();


        this.state = {
                isLoaded:false,
            }
    }


    render() {
        return(
            <div id="guideFeeadback" class="sec-design1">
                <div id="guide_form" class="form-design" name="guide_form">
                    <div id="name-group" class="form-group">
                        <label id="insert-date" class="title-input">     :</label>
                        <input type="date" class="form-control" name="insert-student" id="insert-student" required/>
                    </div>
                    <div id="name-group" class="form-group">
                        <label id="Q1" class="title-input">  :</label>
                        <input type="text" class="form-control" name="Q1" id="Q1" placeholder="Your Answer" minlength="5" required/>
                    </div>
                    <div id="name-group" class="form-group">
                        <label id="Q2" class="title-input">   :</label>
                        <input type="text" class="form-control" name="Q2" id="Q2" placeholder="Your Answer" minlength="5" required/>
                    </div>
                    <div id="name-group" class="form-group">
                        <label id="Q3" class="title-input">  /  :</label>
                        <input type="text" class="form-control" name="Q3" id="Q3" placeholder="Your Answer" minlength="10" required/>
                    </div>
                    <div id="name-group" class="form-group">
                        <label id="Q4" class="title-input" for="name">    :</label>
                        <input type="text" class="form-control" name="Q4" id="Q4" placeholder="Your Answer" minlength="10" required/>
                    </div>
                    <div id ="box" class="chekbox" onSubmit="return checkRadio()">
                        <label id="insert-name" class="title-input">        :</label><br/>
                        <div name="form1" class="chekbox" onSubmit="return checkRadio()">
                            <input type="radio" name="time" value="1"/> <br/>
                            <input type="radio" name="time" value="2"/> <br/>
                            <input type="radio" name="time" value="3"/> <br/>
                        </div>
                    </div>
                    <div id="name-group" class="form-group">
                        <label id="insert-name" class="title-input" for="name">    :</label>
                        <input type="text" class="form-control" name="firstName" id="firstName" placeholder="Your Answer" minlength="10" required/>
                    </div>
                    <button type="submit" id="confirm-form" class="btn btn-info" onClick="fBConfirmation()"> </button>
                    <button id="go-back" class="btn btn-info" onClick="guideLoadMenu()"> </button>
                </div>
            </div>
        )
    }
}





export default test_g_f;
